.store{
  @include to(lg){
    padding: 0 1em;
  }
  &__gallery{
    @include grid('.product-item', 1 3, 1em );
  }
  .text-c{
    color: $color-alt;
  }
  .price-c{
    text-align: center;
    margin-top: -1em;
    position: relative;
    font-size: 1.3em;
    &::before{
      content: '$ ';
    }
    &::after{
      content: '.00';
    }
  }
}

.producto{
  &__img{
    width: 70%;
    max-width: 300px;
    margin: 0 auto 1em;
  }
  &__text{
    padding: 0 1em;
    text-align: center;
    font-size: .9em;
  }
  &__container{
    @include from(m){
      display: grid;
      grid-template-columns: 50% 1fr;
      align-items: center;
    }
    @include from(lg){
      grid-gap: 10em;
      max-width: $max-width;
      margin: 0 auto;
    }
  }
}
.producto__gallery{
  height: auto;
  position: relative;
  margin-bottom: 1em;
  @include from(m){
    margin-bottom: 0;
  }
  .img-slider{
    border: 1px solid $firstColor;
    opacity: .8;
    transition: opacity .3s;
    &:hover{
      opacity: 1;
    }
  }
  .arrow-right , .arrow-left{
    --size: 1em;
    position: absolute;
    width: 1em;
    height: auto;
    left: 1em;
    top: 50%;
    z-index: 50;
    transform: translateY(-50%);
    cursor: pointer;
    @include from(l){
      left: 5em;
    }
  }
  .arrow-right{
    left: unset;
    right: 1em;
    @include from(l){
      right: 5em;
    }
  }
}
.store-slider{
  width: 80%;
  max-width: 350px;
  margin: 0 auto;
  .owl-stage-outer{
    width: calc(100% + 1px) !important;
  }
}
.count-container{
  border: 1px solid $firstColor;
  margin-top: 2em;
  border-radius: .75em;
  display: grid;
  grid-template-columns: repeat(2,50%);
  padding: 1em;
}
.count{
  display: grid;
  grid-template-columns: 1em 3em 1em;
  grid-gap: .5em;
  align-items: center;
  form{
    margin: 0;
  }
  input{
    width: 100%;
    line-height: 2.5rem;
    font-family: inherit;
    font-size: 2em;
    text-align: center;
    border: none;
    background: $firstColor;
    color: #fff;
  }
}
.car-container{
  font-size: 1em;
  .car-img-c{
    height: 1.6em;
    width: 60%;
    margin: 0 auto;
    background: $firstColor;
    border-radius: 1em;
    display: flex;
    justify-content: center;
    align-items: center;
    img{
      height: 90%;
      width: auto;
    }
  }
  span{
    display: block;
    text-align: center;
    font-size: .9em;
  }
}

.total{
  margin-top: 1em;
  text-align: left;
}
#total{
  margin-left: .5em;
  &::before{
    content: "$ ";
  }
}
