.contact{
  margin-bottom: 0 !important;
  @include from(m){
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  &__form{
    padding: 2em;
    background: $firstColor;
    @include from(lg){
      padding: 2em 10vw;
    }
  }
  .section__title{
    color: #fff;
  }
  .input-group{
    margin-bottom: 1.5em;
    border: 1px solid #fff;
    input, textarea{
      width: 100%;
      line-height: 3em;
      border: none;
      font-family: inherit;
      font-size: inherit;
      background: transparent;
      color: #fff;
      padding: 0 2em 0 1em;
    }
    textarea{
      line-height: 1em;
      padding-top: 1em;
      padding-bottom: 1em;
      height: 10rem;
      resize: none;
    }

  }
  &__footer{
    display: grid;
    grid-template-columns: 30% 1fr;
    button{
      display: block;
      border: 1px solid #fff;
      color: #fff;
      background: transparent;
      font-family: inherit;
      line-height: 2.5em;
      width: 100%;
      &:focus, &:active{
        outline: none;
        box-shadow: none;
        border: 1px solid #fff;
      }
    }
  }
  &__map{
    @include to(m){
      height: 100vw;
    }
    img{
      height: 100%;
      width: 100%;
      object-fit: cover;
      object-position: center;
      @include to(m){
        height: 100vw;
      }
    }
  }
}

input::placeholder, textarea::placeholder{
  color: #fff;
}
input{
  &:invalid{
    box-shadow: none;
    border-color: orange;
  }
}

.msg{
  display: flex;
  justify-content: center;
  img{
    visibility: hidden;
    opacity: 0;
    transition: opacity .2s linear;
    &.active{
      visibility: visible;
      opacity: 1;
    }
  }
}
