    transform: rotate(0) translateY(calc(-40% + 1.3em));
  }
}

.empresa__text {
  padding: 0 1em;
  text-align: center;
}

@media screen and (min-width: 640px) {
  .empresa__text {
    padding-right: 2em;
    padding-left: 0;
  }
}

@media screen and (max-width: 1023px) {
  .store {
    padding: 0 1em;
  }
}

.store__gallery {
  display: flex;
  flex-wrap: wrap;
  margin-left: -0.5em;
  margin-right: -0.5em;
}

.store__gallery img {
  display: block;
}

body {
  overflow-x: hidden;
}

.store__gallery > .product-item {
  width: calc(100% - 1em);
  margin: 0.5em;
}

@media screen and (min-width: 640px) {
  .store__gallery > .product-item {
    width: calc(33.33333% - 1em);
  }
}

@supports (grid-template-areas: "header") {
  .store__gallery {
    display: grid;
    grid-gap: 1em;
    margin-left: 0;
    margin-right: 0;
    grid-template-columns: repeat(1, 1fr);
  }
  body {
    overflow-x: visible;
  }
  @media screen and (min-width: 640px) {
    .store__gallery {
      grid-template-columns: repeat(3, 1fr);
    }
  }
  .store__gallery > .product-item {
    width: 100%;
    margin: 0;
  }
}

.store .text-c {
  color: #4F1713;
}

.store .price-c {
  text-align: center;
  margin-top: -1em;
  position: relative;
  font-size: 1.3em;
}

.store .price-c::before {
  content: '$ ';
}

.store .price-c::after {
  content: '.00';
}

.producto__img {
  width: 70%;
  max-width: 300px;
  margin: 0 auto 1em;
}

.producto__text {
  padding: 0 1em;
  text-align: center;
  font-size: .9em;
}

@media screen and (min-width: 640px) {
  .producto__container {
    display: grid;
    grid-template-columns: 50% 1fr;
    align-items: center;
  }
}

@media screen and (min-width: 1024px) {
  .producto__container {
    grid-gap: 10em;
    max-width: 1200px;
    margin: 0 auto;
  }
}

.producto__gallery {
  height: auto;
  position: relative;
  margin-bottom: 1em;
}

@media screen and (min-width: 640px) {
  .producto__gallery {
    margin-bottom: 0;
  }
}

.producto__gallery .img-slider {
  border: 1px solid #c41838;
  opacity: .8;
  transition: opacity .3s;
}

.producto__gallery .img-slider:hover {
  opacity: 1;
}

.producto__gallery .arrow-right, .producto__gallery .arrow-left {
  --size: 1em;
  position: absolute;
  width: 1em;
  height: auto;
  left: 1em;
  top: 50%;
  z-index: 50;
  transform: translateY(-50%);
  cursor: pointer;
}

@media screen and (min-width: 1024px) {
  .producto__gallery .arrow-right, .producto__gallery .arrow-left {
    left: 5em;
  }
}

.producto__gallery .arrow-right {
  left: unset;
  right: 1em;
}

@media screen and (min-width: 1024px) {
  .producto__gallery .arrow-right {
    right: 5em;
  }
}

.store-slider {
  width: 80%;
  max-width: 350px;
  margin: 0 auto;
}

.store-slider .owl-stage-outer {
  width: calc(100% + 1px) !important;
}

.count-container {
  border: 1px solid #c41838;
  margin-top: 2em;
  border-radius: .75em;
  display: grid;
  grid-template-columns: repeat(2, 50%);
  padding: 1em;
}

.count {
  display: grid;
  grid-template-columns: 1em 3em 1em;
  grid-gap: .5em;
  align-items: center;
}

.count form {
  margin: 0;
}

.count input {
  width: 100%;
  line-height: 2.5rem;
  font-family: inherit;
  font-size: 2em;
  text-align: center;
  border: none;
  background: #c41838;
  color: #fff;
}

.car-container {
  font-size: 1em;
}

.car-container .car-img-c {
  height: 1.6em;
  width: 60%;
  margin: 0 auto;
  background: #c41838;
  border-radius: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.car-container .car-img-c img {
  height: 90%;
  width: auto;
}

.car-container span {
  display: block;
  text-align: center;
  font-size: .9em;
}

.total {
  margin-top: 1em;
  text-align: left;
}

#total {
  margin-left: .5em;
}

#total::before {
  content: "$ ";
}

.section.ed-container {
  flex-direction: column !important;
}

button, button:active {
  box-shadow: none;
  border: none;
  outline: none;
  cursor: pointer;
}
