.main-footer{
  background: #1F3469;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  @include from(lg){
    height: 150px;
  }
  > img{
    margin: 0 2em;
    width: auto;
    height: 80%;
  }
  a{
    $size: 3em;
    display: block;
    height: $size;
    width: $size;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
}
