.empresa{
  @include from(m){
    display: grid;
    grid-template-columns: 1fr 60%;
  }
  @include from(lg){
    grid-template-columns: 1fr 50% 1fr;
  }
  &__img {
    max-height: 200px;
    width: auto;
    margin: 0 auto 1em;
    @include from(m){
      max-height: unset;
      position: relative;
      top: 50%;
      transform: translateY(-50%);
    }
    @include from(lg){
      transform: translateY(-50%) rotate(30deg);
    }
    &--right {
      display: none;
      @include from(lg){
        display: block;
        transform: rotate(0) translateY(calc(-40% + 1.3em));
      }
    }
  }
  &__text {
    padding: 0 1em;
    text-align: center;
    @include from(m){
      padding-right: 2em;
      padding-left: 0;
    }
  }
}
