.main-header {
  height: calc(120px + 3em);
  position: relative;
  z-index: 500;
}
.logo-container {
  height: 120px;
  width: 100%;
  background: $firstColor;
  padding-top: 10px;
  img {
    height: 100px;
    width: auto;
    margin: 0 auto;
  }
}
.main-nav {
  height: 3em;
  @include to(lg) {
    padding-top: 1em;
  }
}

.toggle-menu {
  $size: 2em;
  position: relative;
  width: $size;
  height: $size;
  margin: 0 auto;
  @include from(lg) {
    display: none;
  }
}

.main-menu {
  @extend %list-style;
  display: none;
  background: $bodyBg;
  @include from(lg) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    font-family: var(--heading-font);
  }
  li {
    border-bottom: 1px solid rgba(#111, 0.2);
    &:first-child {
      border-top: 1px solid rgba(#111, 0.2);
    }
    @include from(lg) {
      border: none;
    }
  }
  a {
    color: $black;
    line-height: 3em;
    padding-left: 1em;
    display: block;
    @include from(lg) {
      text-align: center;
      padding-left: 0;
    }
    &:hover,
    &.selected {
      color: $firstColor;
    }
  }
}
