.slider {
  position: relative;
  height: calc(#{$width100} * 0.5625);
  .pageContacto & {
    margin-bottom: 0 !important;
  }
  &-title {
    position: absolute;
    width: 100%;
    height: auto;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    display: block;
    padding: 1em 0;
    text-align: center;
    background: rgba(#4f1813, 0.75);
    color: #fff;
    z-index: var(--z-fixed);
    font-family: var(--heading-font);
    @include from(lg) {
      padding: 2em 0;
    }
    > * {
      margin: 0;
    }
    h1 {
      font-size: 1.3em;
      @include from(lg) {
        font-size: 3.5em;
      }
    }
    p {
      font-family: inherit;

      @include from(lg) {
        font-size: 1.7em;
      }
    }
  }
  &__container {
    position: relative;
    height: auto;
    img {
      width: 100%;
      height: auto;
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      transition: opacity.3s;
      &.active {
        opacity: 1;
      }
    }
  }
  &__nav {
    @extend %list-style;
    height: 1em;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 1em;
    z-index: var(--z-fixed);
    li {
      $size: 0.75em;
      background: #fff;
      margin-right: 0.5em;
      border-radius: 50%;
      height: $size;
      width: $size;
      cursor: pointer;
      transition: background 0.2s;
      &:last-child {
        margin-right: 0;
      }
      &.active,
      &:hover {
        background: $firstColor;
      }
    }
  }
}

.banner {
  background: url('../img/banner.jpg') center / cover no-repeat;
  height: calc(#{$width100} * 0.5);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1em;
  text-align: center;
  color: #fff;
  position: relative;
  @include from(m) {
    padding: 2em 2em;
    height: calc(#{$width100} * 0.4);
  }
  @include from(l) {
    padding: 3em 3em;
    height: calc(#{$width100} * 0.35);
  }
  img {
    position: absolute;
    width: $width100;
    height: calc(#{$width100} * 0.65);
    top: 0;
    left: 0;
    object-position: center;
    object-fit: cover;
    opacity: 0.85;
  }
  > * {
    margin-bottom: 0;
    position: relative;
    z-index: 10;
  }
  &:before {
    content: '';
    display: block;
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 5;
    background: rgba(#000, 0.5);
    top: 0;
    left: 0;
  }
  p {
    font-size: 0.7em;
    @include from(m) {
      font-size: 1.2em;
    }
    @include from(lg) {
      font-size: 2em;
    }
  }
  h2 {
    font-size: 0.85em;
    @include from(m) {
      font-size: 1.5em;
    }
    @include from(lg) {
      font-size: 2.5em;
    }
  }
  &__circle-container {
    display: flex;
    justify-content: center;
  }
  &__circle {
    $background: $bullet-bg;
    $size: 7.5vw;
    --size: 7.5vw;
    height: var(--size);
    width: var(--size);
    border-radius: 50%;
    background: $background;
    @include from(m) {
      --size: 5vw;
    }

    &:first-child {
      box-shadow: calc((var(--size) * 2) * -1) 0 0 0 $background,
        calc((var(--size) * 4) * -1) 0 0 0 $background;
      margin-right: 10vw;
    }
    &:last-child {
      box-shadow: calc(var(--size) * 2) 0 0 0 $background,
        calc(var(--size) * 4) 0 0 0 $background;
    }
  }
}

.p-t {
  padding: 2em 0;
  background: #f9f9f9;
  @include from(m) {
    padding: 3em 0;
  }
  @include from(lg) {
    padding: 4em 0;
  }
}

.seleccion {
  width: $width100;
  max-width: $max-width;
  margin-right: auto;
  margin-left: auto;
}
.carrucel-container {
  position: relative;
}

.btn-next,
.btn-prev,
.btn-next1,
.btn-prev1 {
  $size: 2em;
  position: absolute;
  width: $size;
  height: auto;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  z-index: 10;
}
.btn-next,
.btn-next1 {
  left: unset;
  right: 0;
}
.img-c-con {
  padding: 15%;
}

.owl-item .img-c-con {
  transition: transform 0.3s linear;
}
.owl-item.center .img-c-con {
  transform: translateY(-5%) scale(1.15);
}
.img-c-con {
  position: relative;
  &:after {
    $size: 60%;
    content: '';
    position: absolute;
    z-index: -1;
    width: $size;
    height: $size;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -40%);
    background: $firstColor;
    border-radius: 50%;
  }
}
.star .img-c-con:before {
  $size: 10%;
  content: '';
  position: absolute;
  z-index: -1;
  width: $size;
  height: $size;
  top: 28%;
  left: 18%;
  background-image: url('../img/Recomendado.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  //background: lime;
}
p.text-c {
  margin-top: -1em;
  text-align: center;
  font-size: 1.3em;
  @include from(lg) {
    margin-top: -2em;
  }
}
.dos {
  .img-c-con::after {
    background: $bullet-bg;
  }
  p.text-c {
    color: $color-alt;
  }
}

.btn-next1,
.btn-prev1 {
  @include from(m) {
    display: none;
  }
}
